___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: Arial, Helvetica, sans-serif;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 2px solid #dfe0e1
}

.logo {
  color: rgb(119, 119, 119);
}

.nav__search {
  width: 30%;
  height: 3rem;
}

#search {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  border: 1px solid #f5f5f5;
  padding-inline: 1rem;
  font-size: 1.2rem;
  background-color: #f5f5f5;
  color:#737373
}

#search:focus {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border: 1px solid rgba(212, 212, 212, 0.941);
  outline: none;
  background-color: white;
}

main {
  display: flex;
  flex-direction: row;
  gap: 3rem;
}

aside {
  flex: 1;
  height: 20rem;
  position: fixed;
  width: 15rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding-block: 1rem;
  align-items: center;
  font-size: 18px;
  font-weight: 400;
}
aside > section {
  cursor: pointer;
}

.task__list {
  flex: 4;
  margin-left: 16rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  padding-block: 1rem;
}

.selected__nav {
  background-color: #feefc3;
  width: 100%;
  text-align: center;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  padding: 1rem;
}

.add__wrapper {
  border: 2px solid rgb(212, 212, 212);
  width: 50%;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  gap: 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding:0.5rem;
}

#title {
  height: 2.2rem;
  font-size: 17px;
  border: none;
  outline: none;
  padding-inline: 0.5rem;
  display: none;
}

#task__text {
  font-size: 17px;
  resize: none;
  border: none;
  outline: none;
  padding-block: 0.5rem;
  overflow: hidden;
  resize: none;
}


.add__note {
  width: 100%;
  border-top: 1px solid #dfe0e1
}
#addNote {
  position: relative;
  float: left;
  width: 6rem;
  padding: 1rem;
  background-color: #feefc3;
  border: 2px solid #feefc3;
  outline: none;
  cursor: pointer;
  margin-top:0.5rem;
  border-radius: 8px;
  font-size: 17;
}
.notes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2rem;
  padding-inline: 4rem;
  /* justify-content: center; */
}

.notesContainer {
  border: 1px solid #dfe0e1;
  height: auto;
  width: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
  padding: 1rem;
  border-radius: 8px;
}
.notesContainer:hover {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.title {
  color: #292524;
  font-size:17px;
  font-weight:bold;
  opacity: 0.9;
  border:2px solid red
}

.description {
  font-size: 17px;
  color: #292524;
  opacity: 0.8;
}

.footer{
  width:30px;
  height:30px;
  cursor:pointer
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,UAAU;EACV,SAAS;EACT,sBAAsB;AACxB;AACA;EACE,yCAAyC;AAC3C;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,mBAAmB;EACnB,eAAe;EACf;AACF;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,UAAU;EACV,YAAY;AACd;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,yBAAyB;EACzB,oBAAoB;EACpB,iBAAiB;EACjB,yBAAyB;EACzB;AACF;;AAEA;EACE,2CAA2C;EAC3C,4CAA4C;EAC5C,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,OAAO;EACP,aAAa;EACb,eAAe;EACf,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,mBAAmB;EACnB,mBAAmB;EACnB,eAAe;EACf,gBAAgB;AAClB;AACA;EACE,eAAe;AACjB;;AAEA;EACE,OAAO;EACP,kBAAkB;EAClB,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,SAAS;EACT,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,WAAW;EACX,kBAAkB;EAClB,6BAA6B;EAC7B,gCAAgC;EAChC,aAAa;AACf;;AAEA;EACE,oCAAoC;EACpC,UAAU;EACV,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,SAAS;EACT,2CAA2C;EAC3C,cAAc;AAChB;;AAEA;EACE,cAAc;EACd,eAAe;EACf,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,aAAa;AACf;;AAEA;EACE,eAAe;EACf,YAAY;EACZ,YAAY;EACZ,aAAa;EACb,qBAAqB;EACrB,gBAAgB;EAChB,YAAY;AACd;;;AAGA;EACE,WAAW;EACX;AACF;AACA;EACE,kBAAkB;EAClB,WAAW;EACX,WAAW;EACX,aAAa;EACb,yBAAyB;EACzB,yBAAyB;EACzB,aAAa;EACb,eAAe;EACf,iBAAiB;EACjB,kBAAkB;EAClB,aAAa;AACf;AACA;EACE,aAAa;EACb,mBAAmB;EACnB,eAAe;EACf,SAAS;EACT,oBAAoB;EACpB,6BAA6B;AAC/B;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,8BAA8B;EAC9B,SAAS;EACT,aAAa;EACb,kBAAkB;AACpB;AACA;EACE,2CAA2C;AAC7C;AACA;EACE,cAAc;EACd,cAAc;EACd,gBAAgB;EAChB,YAAY;EACZ;AACF;;AAEA;EACE,eAAe;EACf,cAAc;EACd,YAAY;AACd;;AAEA;EACE,UAAU;EACV,WAAW;EACX;AACF","sourcesContent":["* {\r\n  padding: 0;\r\n  margin: 0;\r\n  box-sizing: border-box;\r\n}\r\nbody {\r\n  font-family: Arial, Helvetica, sans-serif;\r\n}\r\n\r\nheader {\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 1.5rem;\r\n  border-bottom: 2px solid #dfe0e1\r\n}\r\n\r\n.logo {\r\n  color: rgb(119, 119, 119);\r\n}\r\n